<template>
    <div id="qr-encode-content">
        <textarea type="text" id="input-qr" disabled/>

        <div class="input-group">
            <div class="custom-file">
                <input type="file" class="custom-file-input" id="inputGroupFile04" aria-describedby="inputGroupFileAddon04" @change="didSelected">
                <label class="custom-file-label" for="inputGroupFile04">Choose file</label>
            </div>
            <!-- <div class="input-group-append">
                <button class="btn btn-outline-secondary" type="button" id="inputGroupFileAddon04" @click="createQR">生成二维码</button>
            </div> -->
        </div>
    </div>
</template>

<script>

import jsQR from "jsqr";

export default {
    data() {
        return {
            qrStr: "",
            logo: "",
        }
    },
    methods: {
        didSelected: function(e) {
            let file = e.target.files[0]
            console.log(file)
            // console.error(e.target.files[0])
            // console.error(this.$refs.fileElem)
            // let file = this.files[0]
            let url = URL.createObjectURL(file);
            this.logo = url
            console.error(url)
            // this.url = url
            // this.$emit("didSelected", url)
            // console.log(code)

            var canvas = document.createElement('canvas')
            canvas.width = 1024
            canvas.height = 1024
            const ctx = canvas.getContext("2d"); 
            
            let image = new Image
            image.src = url
            image.onload = () => {

                ctx.drawImage(image, 0, 0); 
                let imageData = ctx.getImageData(0, 0, 1024, 1024); 
                let code = jsQR(imageData.data, 1024, 1024)

                let content = "未识别到二维码"
                if (code) {
                    content = code.data
                }
                $('#input-qr').val(content)
            }

        },
        createQR: function(e) {
            console.log(this.$router.identify)
            e
            console.log($("#input-qr"))
            let qrStr = $("#input-qr").val()
            this.qrStr = qrStr
            console.log(qrStr)
            $('#qr-content').show()

        },
    }
}
</script>

<style>
#qr-encode-content {
    width: 80%;
}
#input-qr {
    padding: 4px 8px;
    width: 100%;
}
