            <li class="nav-item" role="presentation">
                <a class="nav-link active" id="home-tab" data-toggle="tab" @click="encodeQR()" role="tab" aria-controls="home" aria-selected="true">生成二维码</a>
            </li>
            <li class="nav-item" role="presentation">
                <a class="nav-link" id="profile-tab" data-toggle="tab" @click="decodeQR()" role="tab" aria-controls="profile" aria-selected="false">解析二维码</a>
            </li>
