</template>

<script>


import fastQrEncode from './fast-qr-encode.vue'
import fastQrDecode from './fast-qr-decode.vue'


export default {
    props:{
    },
    data: function(){
        return {path:'/fastQr/encode'}
    },
    created: function() {
        console.log(this.$route.path)
        this.$router.addRoute('fastQr', {
            path:'encode',
            component: fastQrEncode
        })
        this.$router.addRoute('fastQr', {
            path:'decode',
            component: fastQrDecode
        })
        this.updateRouter(this.$data.path)
    },
    updated: function() {
        console.log('mounted')
        this.updateRouter(this.$data.path)
    },
    methods: {
        savePlist: function(e) {


                var a={name:"hello"}
                var b={name:"hi"}
                function exchange(a, b){
                    var c=b; b=a; a=c;
                a.name=a.name+"1"; 
                b.name=b.name+"2"
                console.info(a.name,b.name);
                }
            exchange(a, b); 
            console.info(a.name, b.name);
            console.info("--------");


            //传统写法
            // debugger
            var options = {"say_test":{"name":"1212","times":10}}
            options = {"say_test":{"name":"1212","times":10}}
            this.$http.post('http://localhost:8000/app/', options ,{body:options}).then(function(response){
            // this.$http.get('http://localhost:8000/app/', JSON.stringify({body:options})).then(function(response){
                console.error(response)
                //响应成功回调
            },function(response){
                console.error(response)
                //响应错误回调
            });

            console.error(e)
        },
        updateRouter: function(path) {
            console.warn(this.$route.path)
            console.warn(path)
            if(this.$route.path != path) {
                console.error('正在更新路由')
                this.$router.replace(path, ()=> {
                    this.$data.path = path
                }, error => {
                    console.log(error)
                })
            }
        },
        encodeQR: function() {
            this.updateRouter('/fastQr/encode')
        },
        decodeQR: function() {
            this.updateRouter('/fastQr/decode')
        }
    }
}
</script>

